import type { Metadata } from "next";
import Link from "next/link";
import { CITY_PROFILES } from "@/data/cities";
import { STATE_REGULATIONS } from "@/data/states";

export const metadata: Metadata = {
  title: "City lender guides",
  description:
    "Browse localized lending guides for high-intent US cities. Each page includes regulatory context, borrower personas, and pre-qualification steps.",
};

export default function CitiesIndex() {
  const cityGroups = CITY_PROFILES.reduce<Record<string, typeof CITY_PROFILES>>((acc, city) => {
    const key = city.stateAbbr;
    acc[key] = acc[key] ? [...acc[key], city] : [city];
    return acc;
  }, {});

  const statesWithCities = Object.entries(cityGroups).sort(([a], [b]) => a.localeCompare(b));

  return (
    <div className="mx-auto max-w-6xl space-y-14 px-4 py-16 lg:px-8">
      <header className="space-y-4 text-center">
        <span className="rounded-full bg-white/70 px-4 py-2 text-xs font-semibold uppercase tracking-[0.3em] text-blue-400">
          Local SEO
        </span>
        <h1 className="text-4xl font-semibold text-slate-900">City landing pages</h1>
        <p className="mx-auto max-w-3xl text-base text-slate-500">
          Layer intent + geography + borrower persona for programmatic SEO. Each city template carries localized stats, compliance reminders, and internal links to compare, learn, and lender profiles.
        </p>
      </header>

      <div className="grid gap-6 md:grid-cols-2">
        {statesWithCities.map(([stateAbbr, cities]) => {
          const state = STATE_REGULATIONS.find((item) => item.abbr === stateAbbr);
          return (
            <section
              key={stateAbbr}
              className="space-y-3 rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm"
            >
              <header className="flex items-center justify-between">
                <div>
                  <h2 className="text-2xl font-semibold text-slate-900">
                    {state?.name ?? stateAbbr}
                  </h2>
                  <p className="text-xs text-slate-500">
                    {cities.length} city guides · {state?.paydayAllowed ? "Payday allowed" : "Payday banned"}
                  </p>
                </div>
                <Link
                  href={`/states/${stateAbbr.toLowerCase()}`}
                  className="text-xs font-semibold text-blue-600"
                >
                  View state hub →
                </Link>
              </header>
              <ul className="space-y-2 text-sm text-slate-600">
                {cities.map((city) => (
                  <li key={city.slug} className="flex items-center justify-between rounded-2xl bg-white/70 px-4 py-3">
                    <span>
                      {city.city}, {stateAbbr}
                    </span>
                    <Link
                      href={`/cities/${stateAbbr.toLowerCase()}/${city.slug}`}
                      className="text-xs font-semibold text-blue-600"
                    >
                      Open →
                    </Link>
                  </li>
                ))}
              </ul>
            </section>
          );
        })}
      </div>
    </div>
  );
}
