export function getCitiesByState(stateAbbr: string) {
  return CITY_PROFILES.filter(
    (city) => city.stateAbbr.toLowerCase() === stateAbbr.toLowerCase(),
  );
}

export function getCityProfile(stateAbbr: string, citySlug: string) {
  return CITY_PROFILES.find(
    (city) =>
      city.stateAbbr.toLowerCase() === stateAbbr.toLowerCase() &&
      city.slug === citySlug,
  );
}
